.App {
  text-align: center;
  padding: 2rem;
  background: #111;
  min-height: 100vh;
  color: white;
}

button {
  background-color: #444;
  border: none;
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  margin-bottom: 20px;
  cursor: pointer;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.card {
  position: relative;
  cursor: pointer;
  perspective: 1000px;
}

.card img {
  width: 100%;
  height: 100%;
  display: block;
  border-radius: 10px;
  transition: transform 0.6s;
  backface-visibility: hidden;
}

.card .back {
  position: absolute;
  top: 0;
  left: 0;
}

.card.flipped .front {
  transform: rotateY(0deg);
}

.card .front {
  transform: rotateY(180deg);
  position: absolute;
  top: 0;
  left: 0;
}

.card.flipped .back {
  transform: rotateY(180deg);
}
